.App {
  display: flex;
  flex-wrap: wrap;
}

.app-controls {
  width: 550px;
  height: 90px;
}

.app-controls-and-hand {
  margin: 3px 3px 3px 3px;
}

.app-container {
  position: relative;
  top: 0;
  left: 0;
}

.app-register {
  padding-top: 50px;
  padding-left: 50px;
}

.app-register div {
  padding-top:7px;
}

.app-setplayers {
  width: 570px;
  background-color: #c0c0c0;
  padding: 5px 5px 5px 15px;
  display: flex;
  align-items: flex-start;
  border-radius: 5px;
  margin-bottom: 10px;
}

.app-playerselecter {
  position: relative;
  padding-left: 10px;
  top: 10px;
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.app-log {
  position: relative;
  top: 30px;
  left: 50px;
}
