.myhand-panel {
  width: 580px;
  height: 230px;
  background-color: #c0c0c0;
  padding: 5px 5px 5px 5px;
  display: flex;
  align-items: flex-start;
  border-radius: 5px;
}

.myhand-non-playable-knock {

}

.myhand-playable-knock {
  animation-duration: 1s; 
  animation-fill-mode: both; 
}

@keyframes bounce { 
  0%, 20%, 50%, 80%, 100% {transform: translateY(0);} 
  40% {transform: translateY(-30px);} 
  60% {transform: translateY(-15px);} 
}

.bounce { 
  animation-name: bounce; 
}

.myhand-container {
  position: relative;
  top: 0;
  left: 0;
}

.myhand-card {
  position: absolute;
  top: 60px;
  width: 100px;
  height: 200px;
}

.myhand-card:nth-child(1) {
  left: 10px;
}
.myhand-card:nth-child(2) {
  left: 50px;
}
.myhand-card:nth-child(3) {
  left: 90px;
}
.myhand-card:nth-child(4) {
  left: 130px;
}
.myhand-card:nth-child(5) {
  left: 170px;
}
.myhand-card:nth-child(6) {
  left: 210px;
}
.myhand-card:nth-child(7) {
  left: 250px;
}
.myhand-card:nth-child(8) {
  left: 290px;
}
.myhand-card:nth-child(9) {
  left: 330px;
}
.myhand-card:nth-child(10) {
  left: 370px;
}
.myhand-card:nth-child(11) {
  left: 410px;
}
.myhand-card:nth-child(12) {
  left: 450px;
}
.myhand-card:nth-child(13) {
  left: 490px;
}
