.card-card {
  position: absolute;
  top: 0;
  width: 80px;
  height: 140px;
  background-color: white;
  border-radius: 8px;
  border: solid 1px black;
  padding: 3px 3px 3px 3px;
  display: flex;
  align-items: flex-start;
  transition: top 1s;
}

.card-selected {
  top: -30px;
}

.card-hidden {
  display: none;
  pointer-events: none;
}

.card-not-playable {
  opacity: 0.6;
}

.card-not-playable-grey {
  background-color: #e0e0e0 !important;
}

.card-selected-not-playable {
  animation-duration: 1s; 
  animation-fill-mode: both; 
}

.card-value {
  font-size: 20px;
}

.card-red {
  color: red;
}

.card-black {
  color: black;
}

@keyframes bounce { 
  0%, 20%, 50%, 80%, 100% {transform: translateY(0);} 
  40% {transform: translateY(-30px);} 
  60% {transform: translateY(-15px);} 
}

.bounce { 
  animation-name: bounce; 
}
