.cardtable-panel {
  margin: 3px 3px 3px 3px;
  width: 580px;
  height: 540px;
  background-color: #c0c0c0;
  padding: 5px 5px 5px 5px;
  border-radius: 5px;
}

.cardtable-container {
  position: relative;
  top: 0;
  left: 0;
}

.cardtable-card {
  position: absolute;
  width: 100px;
  height: 200px;
  transition: top 2s;
}

.cardtable-hearts {
  left: 20px;
}
.cardtable-clubs {
  left: 140px;
}
.cardtable-diamonds {
  left: 260px;
}
.cardtable-spades {
  left: 380px;
}

.cardtable-card:nth-child(1) {
  top: 30px;
}
.cardtable-card:nth-child(2) {
  top: 60px;
}
.cardtable-card:nth-child(3) {
  top: 90px;
}
.cardtable-card:nth-child(4) {
  top: 120px;
}
.cardtable-card:nth-child(5) {
  top: 150px;
}
.cardtable-card:nth-child(6) {
  top: 180px;
}
.cardtable-card:nth-child(7) {
  top: 210px;
}
.cardtable-card:nth-child(8) {
  top: 240px;
}
.cardtable-card:nth-child(9) {
  top: 270px;
}
.cardtable-card:nth-child(10) {
  top: 300px;
}
.cardtable-card:nth-child(11) {
  top: 330px;
}
.cardtable-card:nth-child(12) {
  top: 360px;
}
.cardtable-card:nth-child(13) {
  top: 390px;
}

.cardtable-notplayed {
  top: -100px !important;
  pointer-events: none;
}
